<template>
  <div id="app" class="main_div">
    <b-container fluid >
      <b-row>
        <b-col
          :md="main_md"
          :sm="main_sm"
          v-if="main_sw_stat"
          class="main_screen_outer_box_left">
          <div  class="main_screen_inner_box">
            <h2></h2>
          </div>
        </b-col>
        <b-col
        :md="12-main_md"
        :sm="12-main_sm"
        v-if="main_sw_small"
        class="main_screen_outer_box_right">
          <div class="main_screen_inner_box" :style="main_screen_inner_box_padding">
            <main-object></main-object>
          </div>
        </b-col>
      </b-row>
    </b-container>
    <div class="main_sw_class" @click="main_sw_change">
      <s-svg1 v-if="main_sw_stat" class="main_sw_arr"/>
      <s-svg2 v-if="!main_sw_stat" class="main_sw_arr"/>
    </div>
    <div class="test">
      {{testwh}}
    </div>
  </div>
</template>

<script>
import MainObject from './components/object.vue';
import dblAngLeft from './assets/double-angle-left.svg';
import dblAngRight from './assets/double-angle-right.svg';

export default {
  name: 'app',
  components:{
    'main-object': MainObject,
    "s-svg1": dblAngLeft,
    "s-svg2": dblAngRight
  },
  data () {
    return {
      main_sw_stat: true,
      testwh:"",
      main_vw: 0,
      main_sw_small:true,
      main_screen_inner_box_padding:"padding-left:10px"
    }
  },
  methods:
  {
    main_sw_change(){
      this.main_sw_stat=!this.main_sw_stat;
    },
    mainWinWidth() {
      this.testwh = "W: "+this.main_vw;
      if(this.main_vw <576){
        if(this.main_sw_stat===true){
          this.main_sw_small = false;
        }
        else{
          this.main_sw_small = true;
        }
      }
      else this.main_sw_small = true;

      if(this.main_sw_stat===true) this.main_screen_inner_box_padding="padding-left:10px";
      else this.main_screen_inner_box_padding="padding-left:30px;";
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.main_vw = window.innerWidth;
      window.addEventListener('resize', () => {
        this.main_vw = window.innerWidth;
      });
    })
  },
  watch: {
    main_vw: {
      handler: function(){this.mainWinWidth();},
      deep: true
    },
    main_sw_stat: {
      handler: function(){this.mainWinWidth();},
      deep: true
    },
  },
  computed:
  {
    main_md(){
      if(this.main_sw_stat) return 3;
      else return 0;
    },
    main_sm(){
      if(this.main_sw_stat) return 4;
      else return 0;
    }
  }
}
</script>

<style scoped>

.main_screen_outer_box_left{
  padding: 10px 10px;
  padding-right: 5px;
  height: 100vh;
}
.main_screen_outer_box_right{
  padding: 10px 10px;
  padding-left: 5px;
  height: 100vh;
}
.main_screen_inner_box{
  padding: 10px;
  border: 2px green solid;
  height: 100%;
  overflow: auto;
}
.main_div{
  color: white;
  height: 100vh;
  background-image: url(assets/background.jpg);
}
.main_sw_class
{
  position: fixed;
  height:20vh;
  left:0vh;
  width:30px;
  background: black;
  top: 40vh;
  z-index: 250000;
  border: green 1px solid;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main_sw_arr{
  fill: green;
  width:100%;
  height:25%;
}
.test
{
  position: fixed;
  top:10px;
  left:10px;
  width:200px;
  height:30px;
  background: blue;
  color:white;
  z-index: 250000;
}


</style>
