<template>
   <div class="wrn_main ">
     <div class="wrn_box">
        <div class="wrnAlign">
          <h1 class="wrn_warning">!</h1>
        <br>
        </div>
        <div class="wrnAlign">
          <h2 class="wrn_title" v-html="actName"></h2>
        </div>
        <br>
        <div  class="wrnAlign">
          <b-button-group style="width:300px;">
            <b-button variant="primary" v-on:click="wrnActOk" class="wrnyesnobutton" style="width:150px;"> Ok! </b-button>
            <b-button variant="danger" v-on:click="wrnActCancel" class="wrnyesnobutton" style="width:150px;">Cancel</b-button>
          </b-button-group>
        </div>
     </div>
   </div>
</template>
<script>
import {mapGetters} from 'vuex';
import { mapMutations } from 'vuex';
import { mapActions } from 'vuex';

  export default {
    props:[],
    components:{
    },
    data () {
      return {
      }
    },
    methods:{
      ...mapMutations(["actSwitch","wrnSwitch"]),
      ...mapActions(['sendAct']),
      wrnActOk(){
        this.sendAct(this.actActive);
        this.wrnSwitch();
        this.actSwitch({stat: false, name:""});
        return false;
      },
      wrnActCancel(){
        if(this.actStat==false)
        {
          this.wrnSwitch();
          this.actSwitch({stat: false, name:""});
        }
        else this.wrnSwitch();
        return false;
      }
    },
    computed:{
      ...mapGetters(['oneObject','actActive','actStat']),
      actActActive(){
        if(this.actActive in this.oneObject.actions)
          return this.oneObject.actions[this.actActive];
        else {
          return false;
        }
      },
      actName(){
          return this.actActActive.warning;
      },
    }
  }
</script>
