<template>
  <div class="par_main">
    <b-container fluid >
      <b-row no-gutters>
        <b-col  sd="auto" md="auto" class="parAlign">
          <!--      -->
          <icon-ok v-show="false" class="par_icon_filler" />
        </b-col>
        <b-col sd="10" md="10" >
          <span v-if="parDescrShow" class="par_name" v-html="par.description"></span>
        </b-col>
        <b-col sd="1" md="1"></b-col>
      </b-row>
      <b-row no-gutters>
        <b-col sd="auto" md="auto" :class="parAlignStyle">
          <icon-ok v-if="parSignStyle" class="par_icon_ok" />
          <icon-cancel v-if="!parSignStyle" class="par_icon_cancel" />
        </b-col>
        <b-col sd="10" md="10">
          <b-input
            v-if="!parMulti"
            type="text"
            :value="parValue"
            @input="parInput"
            >
          </b-input>
          <div
            v-if="parMulti"
            class="form-control par_textarea">
              <s-editor
                :id="par.code"
                :value="parValue"
                @input="parInput"
                :editorOptions="editorSettings"
                :editorToolbar="editorToolbar">
              </s-editor>
          </div>
        </b-col>
        <b-col sd="1" md="1">
          <b-button
            v-show="parDefault"
            variant="outline-info"
            @click="parSetDefault">
            Default
          </b-button>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>

import VueEditor from './vue2-editor/VueEditor.vue';
import {swW2H,swH2W,swStrip} from './stringlib.js';
import iconOk from '../assets/icon-ok.svg';
import iconCancel from '../assets/icon-cancel.svg';

  export default {
    props:["par"],
    components:{
      's-editor': VueEditor,
      'icon-ok': iconOk,
      'icon-cancel':iconCancel
    },
    data () {
      return {
        parValue: "",
        parInt: "",
        parValid: 0,
        parMin: 0,
        parMax: Number.MAX_SAFE_INTEGER,
        parInputState: "none",
        parMulti: false,
        editorSettings:{},
        editorToolbar: [
          ['bold', 'italic', 'strike'],['image', 'link']
        ]
      }
    },
    mounted(){
      //  - ,   .    
      if(this.par.value.length>0 ) this.parValue=swW2H(this.par.value);
      else if("default_value" in this.par){
        this.parValue=swW2H(this.par.default_value);
      }
      if("restrictions" in this.par){
        if("max_length" in this.par.restrictions) this.parMax=this.par.restrictions.max_length;
        if("min_length" in this.par.restrictions) this.parMin=this.par.restrictions.min_length;
        if("multiline" in this.par.restrictions)  this.parMulti=this.par.restrictions.multiline;
      }
      this.parIsChanged();
    },
    methods:{
      parInput(val){
        this.parValue=val.toString();//.replace(/[^0-9\-]/gm,"");
        this.parIsChanged();
        return this.parValue;
      },
      parIsChanged(){
        let lstr=swH2W(this.parValue);
        if(this.parInt.localeCompare(lstr)!=0){
          this.parInt=lstr;
          this.parEmit();
        }
        let lvalid=this.parIsValid();
        if(this.parValid!=lvalid){
          this.parValid=lvalid;
          this.parEmit();
        }
      },
      parIsValid(){
        if(this.parMax<swStrip(this.parValue).length) return 0;
        if(this.parMin>swStrip(this.parValue).length) return 0;
        return 1;
      },
      parEmit(){
        this.$emit("parChange",{
          "code":this.par.code,
          "value": this.parInt,
          "valid": this.parValid
        });
      },
      parSetDefault(){
        this.parValue=this.par.default_value;
        this.parIsChanged();
      }
    },
    computed:{
      parDescrShow()
      {
        if("description" in this.par) return this.par.description;
        else return false;
      },
      parAlignStyle()
      {
        if(this.parMulti) return "parAlignT";
        else return "parAlign";
      },
      parSignStyle(){
        if(this.parValid === 0) return false;
        else return true;
      },
      parDefault(){
        if("default_value" in this.par) return swW2H(this.par.default_value);
        else return false;
      }
    }
  }
</script>
