Vue.use(Vuex);

export const store = new Vuex.Store({
	state: {
		actStat: false,
		actActive: "",
		wrnStat: false,
		data: {
    }
	},
	getters: {
		oneObject(state){
			return state.data.object;
		},
		actStat(state){
			return state.actStat;
		},
		actActive(state){
			return state.actActive;
		}
	},
	mutations: {
		actSwitch(state,actName)
		{
			if(typeof actName === "string"){
				state.actActive=actName;
				state.actStat=!state.actStat;
			}
			else {
				state.actStat=actName.stat;
				state.actActive=actName.name;
			}
		},
		wrnSwitch(state)
		{
			state.wrnStat=!state.wrnStat;
		},
		addID(state)
		{
			state.data.object.id++;
		},
		dataFill(state)
		{
			state.data=JSON.parse(JSON.stringify(inJson));
		},
		//      
		parSet(state){
			let lact=state.data.object.actions[state.actActive];
			if("valid" in lact) return false;
			else {
				if("user_params" in lact){
					Vue.set(lact,'valid',0);
					let lpar;
					for(lpar in lact.user_params){
						Vue.set(lact.user_params[lpar],'value',NaN);
						Vue.set(lact.user_params[lpar],'valid',0);
					}
				}
				else Vue.set(lact,'valid',1);
			}
			return true;
		},
		//  ,    
		parChange(state,payload){
			let lact=state.data.object.actions[state.actActive];
			let lpar;
			let lvalid=1;
			for(lpar in lact.user_params){
				if(lact.user_params[lpar].code==payload.code)
				{
					lact.user_params[lpar].value=payload.value;
					lact.user_params[lpar].valid=payload.valid;
				}
				lvalid=lvalid*lact.user_params[lpar].valid;
			}
			lact.valid=lvalid;
	  }
	},
	actions: {
		sendAct(store,actName){
			let lact=store.getters.oneObject.actions[actName];
			let lsend={
				request_id: 1,
    		type: "make_action",
    		data : {
					code: lact.code,
					params: lact.params
				}
			}
			if("user_params" in lact){
				lact.user_params.forEach(
					litem => {
						lsend.data[litem.code]=litem.value;
					});
			}
			console.log("Input: "+actName +"\n"+JSON.stringify(lsend));
			alert("Input: "+actName +"\n"+JSON.stringify(lsend));

			// store.commit('orderSend');
			//
			// setTimeout(() => {
			// 	console.log(payload);
			// 	store.commit('orderDone');
			// }, 1000);
		}
	},
	strict: process.env.NODE_ENV !== 'production'
});

store.commit('dataFill');
