<template>
       <div class="act_main ">
         <b-container fluid >
           <b-row>
             <b-col sd="1" md="2" lg="3" class="act_bcol"></b-col>
             <b-col sd="10" md="8" lg="6" class="act_bcol">
               <div class="act_dialog_box">
                  <h2 v-if="actActActive.name" v-html="actActActive.name" class="act_title titleAlign"></h2>
                  <div v-for="par in actActActive.user_params">
                    <s-parint v-if="par.type==='integer'"
                      :par="par"
                      @parChange="parChange">
                    </s-parint>
                    <s-parflt v-if="par.type==='float'"
                      :par="par"
                      @parChange="parChange">
                    </s-parflt>
                    <s-parstr v-if="par.type==='string'"
                      :par="par"
                      @parChange="parChange">
                    </s-parstr>
                    <br>
                  </div>
                  <!-- {{actActActive}} -->
                <br>
                <div class="btnAlign">
                  <b-button-group style="width:300px;">
                      <b-button :disabled="actValid" variant="primary" v-on:click="actOkBtn" style="width:150px;">Ok!</b-button>
                    <b-button variant="danger" v-on:click="actActSwitch" style="width:150px;">Cancel</b-button>
                  </b-button-group>
                </div>
               </div>
             </b-col>
             <b-col sd="1" md="2" lg="3" class="act_bcol"></b-col>
           </b-row>
         </b-container>
       </div>
</template>
<script>
import {mapGetters} from 'vuex';
import { mapMutations } from 'vuex';
import { mapActions } from 'vuex';

import parInt from './par_int.vue';
import parFlt from './par_flt.vue';
import parStr from './par_str.vue';

  export default {
    props:[],
    components:{
      's-parint': parInt,
      's-parflt': parFlt,
      's-parstr': parStr
    },
    data () {
      return {
      }
    },
    created(){
      this.parSet();
    },
    methods:{
      ...mapMutations(["actSwitch","parSet","parChange","wrnSwitch"]),
      ...mapActions(['sendAct']),
      actActSwitch(){
        this.actSwitch("");
        return false;
      },
      actOkBtn(){
        if("warning" in this.actActActive) this.wrnSwitch();
        else{
          this.sendAct(this.actActive);
          this.actSwitch("");
        }
      }
    },
    computed:{
      ...mapGetters(['oneObject','actActive']),
      actActActive(){
        if(this.actActive in this.oneObject.actions)
          return this.oneObject.actions[this.actActive];
        else {
          this.actActSwitch();
          return false;
        }
      },
      actValid(){
        if(this.actActActive.valid==0) return true;
        else return false;
      },
      actName(){
        if("name" in this.actActActive){
          return this.actActActive.name;
        }
        else return false;
      },
    }
  }
</script>
