<template>
  <div>
    <h2 v-if="objTitle" v-html="objTitle" class="obj_title"></h2>
    <h5 v-if="objSubTitle" v-html="objSubTitle" class="obj_subtitle"></h5>
    <div v-if="objTemplate">
      <div v-for="templateItem in objTemplate" class="obj_templategroup">
        <u><p v-if="templateItem.name" v-html="templateItem.name" class="obj-templ-name"></p></u>
        <s-attribute v-for="attr in templateItem.attributes" :attr="oneObject.attributes[attr]" >
        </s-attribute>
        <b-button-group>
          <b-button variant="primary"
            v-for="attr in templateItem.actions"
            v-html="oneObject.actions[attr].name"
            v-on:click="objActSwitch(attr)"
            :disabled="oneObject.actions[attr].disabled">
          </b-button>
        </b-button-group>
      </div>
    </div>
    <br>
    <s-attribute v-for="attr in objAttrs.unused" :attr="oneObject.attributes[attr]">
    </s-attribute>
    <b-button variant="danger"
      v-for="attr in objAttrs.actunused"
      v-html="oneObject.actions[attr].name"
      v-on:click="objActSwitch(attr)"
      :disabled="oneObject.actions[attr].disabled">
    </b-button>
    <s-action v-if="objActStat"></s-action>
    <s-warning v-if="objWrnStat"></s-warning>
  </div>
</template>
<script>
  import singleAttribute from './attribute.vue';
  import singleAction from './action.vue';
  import singleWarning from './warning.vue';
  import {mapGetters} from 'vuex';
  import { mapMutations } from 'vuex';
  import { mapActions } from 'vuex';

  export default {
    components:{
      's-attribute': singleAttribute,
      's-action': singleAction,
      's-warning': singleWarning
    },
    data () {
      return {
        AttrList:{
          used:["title","subtitle"],
          unused:[],
          actused:[],
          actunused:[]
        }
      }
    },
    methods:{
      ...mapMutations(['actSwitch','wrnSwitch']),
      ...mapActions(['sendAct']),
      objActSwitch(actName){
        if("user_params" in this.oneObject.actions[actName])
        {
          this.actSwitch(actName);
        }
        else if ("warning" in this.oneObject.actions[actName]){
          this.actSwitch({name: actName, stat: false});
          this.wrnSwitch();
        }
        else this.sendAct(actName);
        return false;
      }
    },
    computed: {
      ...mapGetters(['oneObject','actStat']),
      objActStat(){
         return this.actStat;
      },
      objWrnStat(){
         return this.$store.state.wrnStat;
      },
      objTitle(){
        if("title" in this.oneObject.attributes){
          return this.oneObject.attributes.title.value;
        }
        else return false;
      },
      objSubTitle(){
        if("subtitle" in this.oneObject.attributes){
          return this.oneObject.attributes.subtitle.value;
        }
        else return false;
      },
      objTemplate(){
        if("groups" in this.oneObject.template){
          return this.oneObject.template.groups;
        }
        else return false;
      },
      objAttrs(){
        if("groups" in this.oneObject.template){
          this.oneObject.template.groups.forEach(
            l_item => {
              if("attributes" in l_item) this.AttrList.used.push(...l_item.attributes);
              if("actions" in l_item) this.AttrList.actused.push(...l_item.actions);
            });
          for(let lkey in this.oneObject.attributes){
            if(this.AttrList.used.indexOf(lkey)<0) this.AttrList.unused.push(lkey);
          }
          for(let lkey in this.oneObject.actions){
            if(this.AttrList.actused.indexOf(lkey)<0) this.AttrList.actunused.push(lkey);
          }
          return this.AttrList;
        }
        else return false;
      }
    }
  }
</script>
<style scoped>
  .obj_title
  {
    margin-left:10px;
  }
  .obj_subtitle
  {
    font-family:monospace;
    margin-left:10px;
    font-style: italic;

  }
  .obj_templategroup
  {
    position:relative;
  	background: rgba(255,255,255,0.25);
  	width:100%;
  	padding:6px 8px;
  	margin:10px 0px;
  	font-family:monospace;
  	font-size:15px;
  	font-weight: bold;
  	font-style: italic;
  }
  .obj_templ_name
  {
    font-family:monospace;
    font-weight: bold;
  }
  .editor-style{
     height:300px;
    overflow:auto;
  }

</style>
