<template>
       <p class="attr_desc" v-if="show" v-html="showValue"></p>
</template>
<script>
import {swW2H,swH2W,swStrip} from './stringlib.js';

  export default {
    props:["attr"],
    data () {
      return {
      }
    },
    computed:{
      show(){
        if( typeof this.attr.value != "string" && typeof this.attr.value != "number") return false;
        else return !this.attr.hidden;
      },
      value(){
        if( typeof this.attr.value_description == "string" || typeof this.attr.value_description == "number")
          return this.attr.value_description;
    		else return this.attr.value;
      },
      showValue(){
        // .   HTMLt
        if("name" in this.attr ) return swW2H("*"+this.attr.name+":* "+this.value);
        return swW2H(this.value);
      }
    }
  }
</script>
<style scoped>
  .attr_desc
  {
  	position:relative;
  	color:white;
    font-style: normal;
    font-weight: normal;
    margin-bottom: 5px;
  }
  /deep/ .attr_desc p{margin-bottom: 0px;}
</style>
